import React from 'react';
import styled from 'styled-components';
import oo from '../images/duotone.png';

function Card({title, desc, img, link, direction}) {




    return (
        <Container >
            <Description>
                <h1>{title}</h1>
                <p>{desc}</p>
            </Description>
            <Image onClick={() => {window.open(link)}}>
                <img src={oo} alt={title} />
            </Image>
        </Container>
    )
}

export default Card;

const Container = styled.div`
    display:flex;
    width: 100%;
    height: 100%;
    margin-bottom: 20px;
    border-radius: 10px;
    transition: all 750ms;
    :hover{
        box-shadow: 0px 0px 28px 2px rgba(48,223,163,0.38);
}
    @media screen and (max-width: 426px){
        flex-direction: column;
    }
`

const Description = styled.div`
    border-radius: 10px;
    width: 50%;
    height:200px;
    padding: 1em;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    @media screen and (max-width: 426px){
        width: 100%;
    }
    h1{
        margin: 0;
    }
`;

const Image = styled.div`
    width: 50%;
    img{
        width: clamp(250px, 100%, 450px);
        height: 100%;
        border-radius: 10px;
    }
    @media screen and (max-width: 426px){
        width: max(250px, 100%);
    }
`;



